(function() {
    'use strict';

    angular
        .module('blackFriday')
        .factory('UserService', UserService);

    /** @ngInject */
    function UserService($window,$http,DeviceService,$q) {
        var factory={};
        factory.userId='';
        factory.userName='';
        factory.userEmail='';
        factory.userScore='';
        factory.typedCoupons=[];

        factory.setUser=function(user)
        {
            this.userId=user.uid;
            this.userName=user.name;
            this.userEmail=user.email;
            this.userScore=user.score;
        };
        factory.getScore=function()
        {
            return this.userScore;
        };
        factory.setScore=function(score)
        {
            this.userScore=score;
        };
        factory.getUserId=function()
        {
            return this.userId;
        };
        factory.getScores=function()
        {
            //console.log(checkSum);
            var deferred = $q.defer();
            $http.post(DeviceService.getBaseURL()+"/api/v1/get_Scores",{checksum:checkSum}).then(
                function(response)
                {
                    if(response.data)
                    {
                        deferred.resolve(response);
                    }
                    else{
                        deferred.reject(response);
                    }
                },function(e)
                {
                    deferred.reject(e);
                }
            );

            return deferred.promise;
        };
        return factory;
    }
})();
