(function() {
    'use strict';

    angular
        .module('blackFriday')
        .factory('DeviceService', DeviceService);

    /** @ngInject */
    function DeviceService($window) {
        var factory={};
        factory.root_Url="";
        factory.dimensions=function()
        {
            return {y:$window.innerHeight,x:$window.innerWidth};
        };
        factory.getBaseURL=function()
        {
           return factory.root_Url;

        };
        factory.setBaseURL=function(url)
        {
            factory.root_Url=url
        };
        return factory;
    }
})();

