(function() {
    'use strict';

    angular
        .module('blackFriday')
        .controller('ResultController', ResultController);

    /** @ngInject */
    function ResultController($timeout,$scope,DataService,$state,$http,DeviceService,UserService,$window) {
        $scope.isWaiting=true;
        $scope.players=[];
        $scope.faceBookShareMessage='';
        $scope.showFBdialog=false;
        $scope.init=function()
        {
            if($window.orientation==0||$window.orientation==180)
            {
                $scope.showWarning=true;
            }
            else
            {
                $scope.showWarning=false;
            }
            $http.post(DeviceService.getBaseURL()+"/api/v1/insert_Score",{bfscore:DataService.getScore(),uid:UserService.getUserId(),checksum:checkSum}).then(
                function(res)
                {
                    console.log(res);
                    UserService.getScores().then(function(resp){
                        if(resp.status==200)
                        {
                            $scope.players=resp.data;
                            console.log(resp.data);
                        }
                    },function(e){

                    });
                    $scope.isWaiting=false;
                },function(e)
                {

                }
            );

        };
        function onOrientationChange4(e)
        {
            if($window.orientation==0||$window.orientation==180)
            {
                $scope.showWarning=true;

            }
            else
            {
                $scope.showWarning=false;

            }
            $scope.$apply();
        }
        $window.addEventListener("orientationchange",onOrientationChange4,false);
        $scope.score=DataService.getScore();
        $scope.rank=DataService.getRank();
        $scope.shareScore=function(type)
        {
            if(type=='FB')
            {
                $scope.faceBookShareMessage='I just participated in the Black Friday Online Training challenge and scored '+$scope.score+'!';
                $scope.showFBdialog=true;
            }
            else if(type=='TW')
            {
                window.open('http://twitter.com/share?url=http://teamdevx.com;text=I just participated in the Black Friday Online Training challenge and scored '+$scope.score+'!');
            }

        };
        $scope.shareToFaceBook=function(faceBookShareMessage)
        {
            console.log(faceBookShareMessage);
            FB.ui({
                method: 'share',
                href: 'http://teamdevx.com',
                message:faceBookShareMessage,
                caption:faceBookShareMessage,
                name:faceBookShareMessage,
                description:' '
            }, function(response){
                console.log(response);
                $scope.faceBookShareMessage='I just participated in the Black Friday Online Training challenge and scored '+$scope.score+'!';
                $scope.showFBdialog=false;
                $scope.$apply();
            });
        };
        $scope.fbshareCancelled=function()
        {
            $scope.faceBookShareMessage='I just participated in the Black Friday Online Training challenge and scored '+$scope.score+'!';
            $scope.showFBdialog=false;
        };
        $scope.playAgain=function()
        {
            DataService.reset();
            $window.removeEventListener("orientationchange",onOrientationChange4,false);
            $state.go('add-to-basket');


        };
        $scope.inviteFriend=function()
        {
            //send email to a friend.
        }
    }
})();

(function() {
  'use strict';

  angular
    .module('blackFriday')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout,$scope,$state,$http,UserService,DeviceService,$window) {
      $scope.info={};
      $scope.isWaiting=true;
      $scope.showWarning=false;
      $scope.initUser='';
      $scope.players=[];

      $scope.init=function()
      {

         // document.cookie='{"uid":"45","username":"test","pj":"bFriday"}';
         // UserService.getScores();

          $http.get("ajs/config.json").then(function(res){
              if(res.status==200)
              {

                  DeviceService.setBaseURL(res.data.root_URL);
                  $scope.start();
              }

          },function(e){

          });

      };
      function onOrientationChange(e)
      {
          if($window.orientation==0||$window.orientation==180)
          {
              $scope.showWarning=true;

          }
          else
          {
              $scope.showWarning=false;

          }
          $scope.$apply();
      }

      $scope.start=function()
      {
          if($window.orientation==0||$window.orientation==180)
          {
              $scope.showWarning=true;
          }
          else
          {
              $scope.showWarning=false;
          }

          $window.addEventListener("orientationchange",onOrientationChange,false);
          UserService.getScores().then(function(res){
              if(res.status==200)
              {
                  $scope.players=res.data;
                  console.log(res.data);
              }
          },function(e){

          });
          var x = document.cookie;
          var str= x.split(';');
          var found=false;

          for(var i=0;i<str.length;i++)
          {
              if(str[i].indexOf('bFriday')>=0)
              {

                  $scope.initUser=JSON.parse(str[i].trim());

                  found=true;
                  break;
              }
          }
          if(found==true)
          {
              $http.post(DeviceService.getBaseURL()+"/api/v1/init/",{uid:$scope.initUser.uid,checksum:checkSum}).then(function(response)
              {
                  if(response.data.email!=null)
                  {
                      console.log($scope.initUser.uid);
                      UserService.setUser({uid:$scope.initUser.uid,name:response.data.name,email:response.data.email,score:response.data.score});
                      $window.removeEventListener("orientationchange",onOrientationChange,false);
                      $state.go("add-to-basket");
                  }
                  else
                  {
                      $scope.isWaiting=false;
                  }
              },function(e)
              {
                  $scope.isWaiting=false;
              });

          }
          else
          {
              $scope.isWaiting=false;
          }
      }
      $scope.validateEmail=function(email)
      {

              var x = email;
              if(x)
              {
                  var atpos = x.indexOf("@");
                  var dotpos = x.lastIndexOf(".");
                  if (atpos<1 || dotpos<atpos+2 || dotpos+2>=x.length) {
                      alert("Not a valid e-mail address");
                      return false;
                  }
              }
              else{
                  alert("Not a valid e-mail address");
                  return false;
              }
            return true;

      };
      $scope.takeChallege=function()
        {
           console.log($scope.info.email,$scope.info.name,$scope.info.agree);
            if($scope.validateEmail($scope.info.email)&&$scope.info.name!=''&&$scope.info.agree==true)
            {
                $http.post(DeviceService.getBaseURL()+"/api/v1/insert_User",{bfname:$scope.info.name,bfemail:$scope.info.email,checksum:checkSum}).then(
                    function(response)
                    {
                        console.log("res",response);
                        if(response.data!=null)
                        {
                            $http.post(DeviceService.getBaseURL()+"/api/v1/login_user",{bfname:$scope.info.name,bfemail:$scope.info.email,checksum:checkSum}).then(
                                function(res)
                                {
                                    if(res.data!=null)
                                    {
                                        UserService.setUser({uid:response.data,name:$scope.info.name,email:$scope.info.email,score:0});
                                        document.cookie='{"uid":"'+response.data+'","username":"'+$scope.info.name+'","pj":"bFriday"}';
                                        $window.removeEventListener("orientationchange",onOrientationChange,false);
                                        $state.go("add-to-basket");
                                    }
                                },function(e)
                                {
                                    console.log(e);
                                }
                            );

                        }
                    },
                    function(e)
                    {
                        console.log(e);
                    }
                );

            }

        };
  }
})();

(function() {
    'use strict';

    angular
        .module('blackFriday')
        .controller('CheckoutController', CheckoutController);

    /** @ngInject */
    function CheckoutController($timeout,$scope,DeviceService,DataService,$window,$state) {

        $scope.test={};
        var startingTime=null;
        var totalElapsedTime=0;
        var dim=DeviceService.dimensions();
        var basket_com=angular.element( document.querySelector( '#basket-container' ) );
        var playArea=angular.element( document.querySelector( '#basket-play' ) );
        playArea.addClass("blurred");
        basket_com.height(dim.y);

        var game=angular.element( document.querySelector( '#game' ) );

        var code_div=angular.element( document.querySelector( '#code' ) );

        $(document).ready(function(){
            $('#codeTyped').bind("cut copy paste",function(e) {
                e.preventDefault();
            });
            $('#code').bind("cut copy paste",function(e) {
                e.preventDefault();
            });
        });

        if($window.orientation==0||$window.orientation==180)
        {
            $scope.showWarning2=true;

        }
        else
        {
            $scope.showWarning2=false;
        }
        function onOrientationChange3(e)
        {
            if($window.orientation==0||$window.orientation==180)
            {
                $scope.showWarning2=true;
                $scope.pauseGame();
            }
            else
            {


                $scope.showWarning2=false;
                $scope.resumeGame();


            }
            //  alert("ok "+$scope.showWarning2+$window.orientation);
            $scope.$apply();
        }
        $window.addEventListener("orientationchange",onOrientationChange3,false);

        $scope.couponCodes=[];

        $scope.codeTyped='';

        $scope.codeDisplayed=DataService.getRandomCode();

        $scope.items=DataService.getSelectedItems();


        $scope.countdownCount=0;
        $scope.startCountDown=false;
        $scope.gameStarted=false;
        $scope.timedout=false;
        $scope.totalTime=30;

        $window.addEventListener("keyup",onEnterUp,false);

        function onEnterUp(e)
        {


            if($scope.timedout==false)
            {
                var keyCode = e.keyCode;
                if(keyCode==13) {
                    console.log("gfhfhgfhgfhfgh  "+$scope.codeTyped+"    "+$scope.codeDisplayed);
                    if($scope.codeTyped==$scope.codeDisplayed)
                    {
                        code_div.removeClass('addRed');
                        $scope.couponCodes.push($scope.codeDisplayed);
                        $scope.codeDisplayed=DataService.getRandomCode();
                        $scope.codeTyped='';
                    }
                    else
                    {
                        code_div.addClass('addRed');
                        // change color to red
                    }
                    $scope.$apply();
                }
            }


        }
        $scope.pauseGame=function(e,obj)
        {

            $scope.gamePaused=true;
            startingTime=null;

        };
        $scope.resumeGame=function(e,obj)
        {
            $scope.gamePaused=false;
            startingTime=null;
            if($scope.startCountDown==true)
            {
                requestAnimationFrame($scope.step);
            }

        };
        $scope.onStart=function()
        {
            game.height('90%');
            $scope.startCountDown=true;
            $scope.gameStarted=false;
            $scope.gamePaused=false;
            window.requestAnimationFrame($scope.step);

        };
        $scope.step=function(ts)
        {
            if(startingTime==null) {
                startingTime = ts;
            }

            totalElapsedTime+=(ts-startingTime);

            startingTime = ts;

            if($scope.gamePaused==false)
            {

                if($scope.gameStarted==true)
                {

                    $scope.totalTime=31-Math.ceil((totalElapsedTime)/1000)+3;

                    if((31-Math.ceil((totalElapsedTime)/1000)+3)<=0)
                    {
                        $scope.timedout=true;
                        $window.removeEventListener("keyup",onEnterUp,false);
                        DataService.addCoupons($scope.couponCodes);
                        $window.removeEventListener("orientationchange",onOrientationChange3,false);
                        $state.go('results');
                    }
                }
                else if($scope.startCountDown==true)
                {
                    console.log(4-Math.ceil(totalElapsedTime/1000));
                    $scope.countdownCount=(4-Math.ceil(totalElapsedTime/1000));
                    if(4-Math.ceil((totalElapsedTime)/1000)<=0)
                    {
                        $scope.gameStarted=true;
                        playArea.removeClass("blurred");

                    }
                }
                if($scope.timedout==false)
                {
                    requestAnimationFrame($scope.step);
                }
            }

            $scope.$apply();
        };




    }
})();

(function() {
    'use strict';

    angular
        .module('blackFriday')
        .controller('BasketController', BasketController);

    /** @ngInject */
    function BasketController($timeout,$scope,DeviceService,DataService,$state,$window,$rootScope) {
        var dim,basket_com,basket_countDown,playArea,game,addMax,addmin,durMin,durMax,count,element_indices;
        var startingTime=null;
        var totalElapsedTime=0;
        $scope.init=function()
        {

            $scope.showWarning2=false;
            $scope.firsttime=true;
            $scope.prev=0;
            addMax=40;
            addmin=5;
            durMin=80;
            durMax=110;

            $scope.aspectR=4/3;
            $scope.cols=6;
            $scope.rows=6;
            $scope.gapPercent=1;
            $scope.hudheight=100;

            $scope.elements=[];
            $scope.addedItems=[];
            element_indices=0;
            dim=DeviceService.dimensions();
            $scope.products=DataService.getItems();
            var basket_main=angular.element( document.querySelector( '#basket-main' ) );
            basket_main.height(dim.y);

            basket_com=angular.element( document.querySelector( '#basket-container' ) );
           // basket_com.height(dim.y);
            basket_countDown=angular.element( document.querySelector( '#basket-countDown' ) );
            playArea=angular.element( document.querySelector( '#basket-play' ) );
            game=angular.element( document.querySelector( '#game' ) );


            if($window.orientation==0||$window.orientation==180)
            {
                $scope.showWarning2=true;

            }
            else
            {
                $scope.firsttime=false;

                $scope.showWarning2=false;

                playArea.addClass("blurred");

                playArea.height(game.height());
                playArea.width(playArea.height()*$scope.aspectR);

                $scope.countdownCount=0;
                $scope.startCountDown=false;
                $scope.gameStarted=false;
                $scope.timedout=false;
                $scope.totalTime=30;
                $scope.addTimeout=0;
                $scope.removeTimeout=0;
                $scope.gamePaused=false;
            }
        };

        function onResizing(e)
        {
            angular.element( document.querySelector( '#basket-main' )).height(DeviceService.dimensions().y);
            $scope.adjustScreenElements();
            $scope.$apply();
        }

        function onOrientationChange2(e)
        {
            if($window.orientation==0||$window.orientation==180)
            {
                $scope.showWarning2=true;
                $scope.pauseGame();
            }
            else
            {
                if($scope.firsttime==true)
                {
                    $scope.firsttime=false;

                    $scope.showWarning2=false;
                    $scope.$apply();


                    playArea.addClass("blurred");
                    playArea.height(game.height());
                    playArea.width(playArea.height()*$scope.aspectR);


                    $scope.countdownCount=0;
                    $scope.startCountDown=false;
                    $scope.gameStarted=false;
                    $scope.timedout=false;
                    $scope.totalTime=30;
                    $scope.addTimeout=0;
                    $scope.removeTimeout=0;
                    $scope.gamePaused=false;
                }
                else
                {
                    ///alert("ok");
                    $scope.showWarning2=false;
                    $scope.resumeGame();
                }

            }
            //  alert("ok "+$scope.showWarning2+$window.orientation);
            $scope.$apply();
        }


        $window.addEventListener("orientationchange",onOrientationChange2,false);

        $scope.onStart=function()
        {
            game.height('90%');

            playArea.height(game.height());
            playArea.width(playArea.height()*$scope.aspectR);
            var playAreaHeight=playArea.height();
            $scope.minGap=10;
            $scope.rows=Math.ceil($scope.cols*((playAreaHeight-$scope.hudheight)/playArea.width()));

            $scope.proheight=(playAreaHeight-$scope.hudheight-($scope.minGap*($scope.rows+1)))/$scope.rows;
            $scope.gapvert=$scope.minGap+((playAreaHeight-$scope.hudheight-($scope.minGap*($scope.rows+1)))-($scope.proheight*$scope.rows))/($scope.rows+1);
            $scope.proWidth=$scope.proheight;
            $scope.gaphori=$scope.minGap+((playArea.width()-$scope.minGap*($scope.cols+1))-$scope.proWidth*$scope.cols)/($scope.cols+1);
            $scope.positions=[];

            for(var i=0;i<$scope.rows;i++)
            {
                for(var j=0;j<$scope.cols;j++)
                {
                    $scope.positions.push({x:j,y:i});
                }
            }
           // console.log(playAreaHeight,$scope.hudheight,playArea.width());

            $scope.startCountDown=true;
            $scope.gameStarted=false;
            $scope.gamePaused=false;
            window.requestAnimationFrame($scope.step);
          //  setCountDown();
        };
        $scope.adjustScreenElements=function()
        {
            playArea.height(game.height());
            playArea.width(playArea.height()*$scope.aspectR);
            var playAreaHeight=playArea.height();
            $scope.minGap=10;
            $scope.rows=Math.ceil($scope.cols*((playAreaHeight-$scope.hudheight)/playArea.width()));

            $scope.proheight=(playAreaHeight-$scope.hudheight-($scope.minGap*($scope.rows+1)))/$scope.rows;
            $scope.gapvert=$scope.minGap+((playAreaHeight-$scope.hudheight-($scope.minGap*($scope.rows+1)))-($scope.proheight*$scope.rows))/($scope.rows+1);
            $scope.proWidth=$scope.proheight;
            $scope.gaphori=$scope.minGap+((playArea.width()-$scope.minGap*($scope.cols+1))-$scope.proWidth*$scope.cols)/($scope.cols+1);
            $scope.positions=[];

            for(var i=0;i<$scope.rows;i++)
            {
                for(var j=0;j<$scope.cols;j++)
                {
                    $scope.positions.push({x:j,y:i});
                }
            }
            for(i=0;i<$scope.elements.length;i++)
            {
                if($scope.elements[i].isvisible==true)
                {
                    console.log("ghjghjhjhjhj");
                    $scope.elements[i].element.style.width=$scope.proheight+'px';
                    $scope.elements[i].element.style.height=$scope.proWidth+'px';
                    $scope.elements[i].element.style.top=($scope.hudheight+$scope.gapvert+$scope.elements[i].pos.y*($scope.proheight+$scope.gapvert))+'px';
                    $scope.elements[i].element.style.left=($scope.gaphori+$scope.elements[i].pos.x*($scope.proWidth+$scope.gaphori))+'px';
                }

            }

        };
        $window.addEventListener('resize',onResizing,false);
        $scope.onResizing=function(e)
        {
            angular.element( document.querySelector( '#basket-main' )).height(DeviceService.dimensions().y);
            $scope.adjustScreenElements();
            $scope.$apply();
        };
        $scope.addIcon=function()
        {

            var element=document.createElement("div");
            element.style.width=$scope.proheight+'px';
            element.style.height=$scope.proWidth+'px';
            element.style.background='#aef321';
            element.style.position='absolute';

            var ran=parseInt(Math.random()*$scope.positions.length);

            var pos=$scope.positions.splice(ran,1);

            element.style.top=($scope.hudheight+$scope.gapvert+pos[0].y*($scope.proheight+$scope.gapvert))+'px';

            element.style.left=($scope.gaphori+pos[0].x*($scope.proWidth+$scope.gaphori))+'px';

            var add=durMin+Math.random()*(durMax-durMin);

            playArea[0].appendChild(element);
            $scope.elements.push({element:element,pos:pos[0],element_index:element_indices,productId:$scope.products[parseInt($scope.products.length*Math.random())].id,add:add,isvisible:true});
            element.setAttribute('data-index',element_indices);
            element.addEventListener("click",function(e)
            {

                if($scope.timedout==false)
                {
                    for(var j=0;j<$scope.elements.length;j++)
                    {

                        if($scope.elements[j].element_index==parseInt(e.target.getAttribute('data-index')))
                        {
                            if($scope.elements[j].add>0)
                            {

                                $scope.addedItems.push($scope.elements[j].productId);
                                $scope.elements[j].element.style.display='none';
                                $scope.elements[j].isvisible=false;
                                $scope.positions.push($scope.elements[j].pos);


                            }
                            break;
                        }
                    }
                    $scope.$apply();
                }
            },false);
            element_indices++;
            pos=null;

        };

        $scope.step=function(ts)
        {
            if(startingTime==null) {
                startingTime = ts;
            }

            totalElapsedTime+=(ts-startingTime);

            startingTime = ts;

            if($scope.gamePaused==false)
            {

                if($scope.gameStarted==true)
                {

                    $scope.totalTime=31-Math.ceil((totalElapsedTime)/1000)+3;
                    for(var i=0;i<$scope.elements.length;i++)
                    {
                        if($scope.elements[i].add>0)
                        {
                            $scope.elements[i].add--;
                        }
                        if($scope.elements[i].add<=0&&$scope.elements[i].isvisible==true)
                        {
                            $scope.elements[i].element.style.display='none';
                            $scope.elements[i].isvisible=false;
                            $scope.positions.push($scope.elements[i].pos);
                        }

                    }
                    $scope.addTimeout--;



                    if($scope.addTimeout<=0) {
                        $scope.addTimeout = addmin + Math.random() * (addMax - addmin);
                        if ($scope.positions.length > 0) {
                            $scope.addIcon();
                        }

                    }
                    if((31-Math.ceil((totalElapsedTime)/1000)+3)<=0)
                    {
                        $scope.timedout=true;
                    }
                }
                else if($scope.startCountDown==true)
                {
                    //console.log(totalElapsedTime/1000,4-Math.ceil(totalElapsedTime/1000))
                    $scope.countdownCount=(4-Math.ceil(totalElapsedTime/1000));
                    if(4-Math.ceil((totalElapsedTime)/1000)<=0)
                    {
                        $scope.gameStarted=true;
                        $scope.addTimeout=addmin+Math.random()*(addMax-addmin);
                        $scope.addIcon();
                        playArea.removeClass("blurred");

                    }
                }
                if($scope.timedout==false)
                {
                    requestAnimationFrame($scope.step);
                }
            }

            $scope.$apply();
        };

        $scope.pauseGame=function(e,obj)
        {

            $scope.gamePaused=true;
            startingTime=null;

        };
        $scope.resumeGame=function(e,obj)
        {
            $scope.gamePaused=false;
            startingTime=null;
            if($scope.startCountDown==true)
            {
                requestAnimationFrame($scope.step);
            }

        };


        $scope.checkout=function()
        {
            $window.removeEventListener('resize',onResizing,false);
            $window.removeEventListener("orientationchange",onOrientationChange2,false);
            DataService.addItems($scope.addedItems);
            $state.go('coupon-typing');
        }

    }
})();
