(function() {
    'use strict';

    angular
        .module('blackFriday')
        .controller('RootController', RootController);

    /** @ngInject */
    function RootController($timeout,$scope,DataService,$state,$http,DeviceService,UserService,$window) {
            $scope.showWarning=false;
       // alert($window.orientation);
        if($window.orientation==0)
        {
            $scope.showWarning=true;
            $scope.$broadcast('pauseScreen');
        }
        else
        {
            $scope.showWarning=false;
        }
        $window.addEventListener("orientationchange",function(){
            if($window.orientation==0)
            {
                $scope.showWarning=true;
                $scope.$broadcast('pauseScreen');
            }
            else
            {
                if($scope.showWarning==true)
                {
                    $scope.$broadcast('resumeScreen');
                }
                $scope.showWarning=false;
            }
            $scope.$apply();
            alert("working");
        });
    }
})();
